.signup-container-all{
       background-color: rgb(54, 55, 54);
          ;display: flex;
          justify-content: center;
}

.signup-info-container{
    width: 31%;
    min-width: 500px;
    color: rgb(170, 169, 169);  
}

.signup-info-container p{
    font-size: smaller;
    line-height: 24px;
     text-align:center;

}


.signup-info-container h1{
  color: white;
  margin-bottom: 10px;
}


.signup-info-container h3{
font-weight: 400
}

.signup-email{
    display: inline-block;
   background-color: rgb(54, 55, 54);
   border: rgb(116, 116, 116) 1px solid;
   padding: 10px;
   width: 85%
}

.signup-email::placeholder{
color: rgb(201, 201, 201);
}




.email-container button{
font-weight: 400;
padding: 10px;
}

.email-container {

margin: 20px;
}

@media only screen and (max-width: 700px) {
.signup-info-container{
    width: 31%;
    min-width: 500px;
    padding: 0 15px;
    color: rgb(170, 169, 169); 
}


.email-container{
    display: flex;
    flex-direction: column;
}
.email-container button{
  width: 100%
}

.signup-email{
 
 

   width: 100%
}

}







