.mountain-image-and-header {
  position: relative;
  height: auto;
 
  width: 100%;

  overflow: hidden;
  max-height: 800px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.mountain-image-and-header img {
  position: relative;

  margin-left: auto;
  margin-right: auto;
  max-width: 140vw;
  min-height: 500px;
 
  margin: auto;
  top: -50px
}

.mountain-header-button-cont {
  position: absolute;

  color: rgb(255, 0, 0);
  z-index: 10;

}

.mountain-header-button-cont h1 {
  color: white;
  font-weight: 800;
  font-family: "Open Sans", sans-serif;
}

.mountain-header-button-cont a {
  display: inline-block;
  color: rgb(0, 0, 0);
  font-weight: 600;
  border: white 2px solid;
  padding: 5px 15px;

  background-color: white;
  text-decoration: none;
  margin-top: 20px;
}

@media only screen and (max-width: 620px) {
  .mountain-header-button-cont a {
    padding: 5px 125px;
  }



  .mountain-header-button-cont  h1{
    font-size: 28px
}


.mountain-image-and-header {
     height: auto
}


.mountain-image-and-header img {
    top: 0;
}
}
