

.gen-image-cont{
    position: relative;
}
.generation-img-and-text-cont{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}



.generation-header{
    font-size:larger;
    padding: 50px 0;
}




.generation-text-and-button{
position: absolute;

color: rgb(255, 255, 255);
font-size: larger;
}

.generation-text-and-button a{

color: black;
background-color: white;
text-decoration: none;
padding: 10px 15px;
display: inline-block;
margin-top: 15px;

}


.gen-image-cont{
    display: flex;
    justify-self: center;
    
    width: 100vw;

    height: 40vw;
    min-height: 700px;
    overflow: hidden;
}

.gen-image-cont img{

width:2000px;

object-fit: cover
    
  
}

@media only screen and (max-width: 800px) {
    .gen-image-cont{
    
    
   
    border: rgb(10, 8, 8) 2px solid;
    height: 80vw;
    min-height: 00px;
    overflow: hidden;
}

.gen-image-cont img{

width:1000px;

object-fit: cover
    
  
}

.generation-header{
    font-size:smaller;
    padding: 50px 0;
}

.generation-text-and-button a{

display: block;
padding: 8px 15px;

}

.generation-text-and-button{


  padding: 0 20px;

font-size: larger;
}

.generation-text-and-button h1{
    font-size: 28px;
}






}