.hscomponentcontainer {

}

body {
  justify-content: center;
  overflow-x: hidden;
}

.columncont {

  display: flex;
  justify-content: center;
}

.shoesmaincontainer,
.helmetmaincontainer {
  
  width: 585px;
  margin: 30px;
}

.shoesimgcontainer img,
.helmetimgcontainer img {
  width: 100%;
}

.helmetinfo,
.helmetlink,
.shoesinfo,
.shoeslink {
  text-align: left;

}



.shoeslink a,
.helmetlink a {
  text-decoration: none;
  font-family: Arial, Helvetica, sans-serif;
  font-size: smaller;
}

@media only screen and (max-width: 1200px) {
  .columncont {

    display: flex;
    justify-content: center;
  
  }

  .shoesmaincontainer,
  .helmetmaincontainer {

    width: 85%;
    margin: 20px;
  }

  .helmetinfo,
.shoesinfo {
  font-size: smaller;
}
}











@media only screen and (max-width: 700px) {
  .columncont {
  
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }

  .shoesmaincontainer,
  .helmetmaincontainer {
   
    
    margin: 20px;
  }
}
