.runup-container {
  display: flex;
  justify-content: center;
  background-color: black;
}
.runup-right-side {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
}

.runup-image,
.runup-right-side {

  width: 50%;
}

.runup-image img {
  width: 100%;
  height: 100%;
}

.runup-heading-button-cont {

  width: 40%;

  color: white;
}

.runup-heading-button-cont a {
  text-decoration: none;
  padding: 10px 15px;

  color: black;
  background-color: white;
  display: inline-block;
  margin-top: 20px;
}

@media only screen and (max-width: 800px) {
  .runup-container {
  flex-direction: column
  }

  .runup-image,
.runup-right-side {

  width: 100%;
}

.runup-heading-button-cont {

  width: 98%;


}




.runup-heading-button-cont a {
 display: block;
 margin-bottom: 15px;



}

}
