
.footerlinks-comp-cont{
    display: flex;
    justify-content: center;
      background-color: rgb(54, 55, 54);
      color: white;
 
}


.footerlinks-cont {
    display: flex;
    flex-direction: row;
     border-bottom: .1px solid rgb(82, 80, 80);
          border-top: 1px solid rgb(82, 80, 80);
     width: 100%;
     max-width: 1200px;
     padding: 20px 0;
     text-align: left;
     justify-content: space-between;
          margin: 20px;

}

.footerlinks-cont li {
    display: flex;
    flex-direction: row;
color: rgb(206, 204, 204);
     list-style: none;
     font-size: small;
     font-weight: normal;
     font-style: italic;
     line-height: 22px;
     cursor: pointer;

}


.footerlinks-cont li:hover {
 text-decoration: underline

}



.footerlinks-cont h3 {


display: inline-block;
font-size: small;

     margin-bottom: 15px;
}


@media only screen and (max-width: 640px) {

.footerlinks-cont {

    flex-direction: column;
 
}


.footerlinks-cont h2{

   border-bottom: 1px solid rgb(102, 102, 102)
 
}

    
.footerlinks-cont ul{
display: none
}


.footerlinks-cont h2:hover  ul{

display: block
 
}


}



