.row1-container {
  display: flex;
  justify-content: center;
}

.duck-container,
.plastic-container {

  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 50%;
}

.duck-container img,
.plastic-container img {

  width: 100%;
}

.row1-header-container {
  position: absolute;

  width: 80%;
}

.row1-header-container a {
  display: inline-block;
  text-decoration: none;

  color: white;
  background-color: black;
  font-weight: bold;
  padding: 10px 15px;
  margin-top: 15px;
}

.row1-header-container h1 {
  font-size: 40px;
}

.row1-header-container p {
  font-weight: bold;
}

.duck-container .row1-header-container {
  color: white;
}

.duck-container .row1-header-container a {
  color: black;
  background-color: white;
}

@media only screen and (max-width: 800px) {
  .row1-container {
    flex-direction: column;
  }


 .row1-header-container a {
padding: 10px 90px
}
}
