* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.navcontainer {
  position: fixed;
  z-index: 888;
}

.gnav-cont {
  background-color: rgb(54, 55, 54);
  display: flex;
  justify-content: space-between;
  padding: 3px 25px;
  align-items: center;
  position: sticky;
  width: 100vw;
}

.flag-cont img {
  height: 20px;
  width: 20px;
  border-radius: 50%;
}

.gnav-user-cart-flag {
  display: flex;
}

.gnav-user-cart-flag i {
  font-size: 20px;
  cursor: pointer;
  color: white;
  margin-left: 14px;
}

.gnav-logo-cont {
  display: flex;
}
.gnav-logo-cont h1 {
  margin-right: 16px;
  font-size: 19px;
  color: grey;
  cursor: pointer;
}

.gnav-logo-cont h1:hover {
  color: rgb(242, 242, 242);
}

.gnav-trek {
  font-style: italic;
  font-weight: 800;
  padding-bottom: 5px;
  font-size: 19px !important;
}

.gnav-electra {
  font-family: "Dancing Script", cursive;
  font-size: 22px !important;
}

.gnav-BONTRAGER {
  font-style: italic;
  padding-top: 3px;
  font-weight: 800;
  font-size: 17px !important;
  vertical-align: middle;
}

.travel {
  font-family: "IM Fell English SC", serif;
  font-weight: 400;
  font-size: 16px;
  font-style: normal;
  margin-left: 5px;
}

@media only screen and (max-width: 1020px) {
  .gnav-cont {
    display: none;
  }
}

/* ///////////////////////// */

.blacknav-cont {
  background-color: black;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 15px;

  width: 100vw;
  z-index: 1;
}

.bnav-icon {
  font-style: italic;
  font-weight: 800;
  font-size: 22px;
  transform: scale(2, 1);
  padding: 0px 28px;
  display: inline-block;
  cursor: pointer;
}

.bnav-categ {
  display: flex;
  font-size: 12px;
  justify-content: space-between;
}

.bnav-categ h3 {
  display: flex;
  font-size: 14px;
  margin: 12px;
  cursor: pointer;
  font-weight: 800;
  font-family: "Roboto", sans-serif;
}

.bnav-categ h3:hover {
  text-decoration: underline;
}

.search-input-icon-wrapper {
  background-color: black;
  border: rgb(70, 70, 70) 1px solid;
  display: flex;
  align-items: center;
  padding: 7px;
  font-size: 20px;
}

.search-input-icon-wrapper:focus-within {
  background-color: rgb(255, 255, 255);
  color: black;
}
.search-input-icon-wrapper:focus-within,
.bnav-search input[type="text"]::placeholder {
  color: black;
}

.bnav-search input[type="text"] {
  background-color: black;
  padding-top: 4px;
  padding-left: 6px;
  border: none;
  outline: none;
}

.bnav-search input[type="text"]::placeholder {
  font-style: italic;
  color: rgb(209, 209, 209);
  font-size: 18px;
}

.bnav-search input[type="text"]:focus {
  background-color: white;
}

.fa-search {
  color: rgb(213, 210, 210);

  font-size: 19px;
}

@media only screen and (max-width: 1020px) {
  .bnav-categ,
  .bnav-search input[type="text"],
  .fa-search,.bnav-search-cont{
    display: none;
    color: red;
  }
  .blacknav-cont {
    justify-content: center;
  }
}
