.vid-heading-text {
  position: absolute;
  top: 45%;
  left: 0;
  text-align: center;
  width: 100%;
  border: 50%;
  font-weight: bold;

  font-size: larger;
  color: white;
}

.vid-heading-text a {
  position: relative;
  display: inline-block;

  margin-top: 10px;
  color: rgb(68, 68, 68);
  text-decoration: none;
  font-size: smaller;
  padding: 8px 18px;
  background-color: rgb(235, 235, 235);
}

.vid-heading-text a:hover {
  background-color: rgb(220, 220, 220);
}

.video-heading-cont {
  display: flex;
  align-items: center;
  justify-content: center;

  height: auto;
}
